import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const Demo = function () {
  return (
    <div>
      <h2>Calendar Inline Display Without Input</h2>
      <Datepicker
        inline
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo11')
);
